.footer {
  color: white;
  text-align: center;
  padding: 10px 0;
  width: 100vw;
  box-sizing: border-box;
  box-shadow: 0 0px 20px rgba(0, 0, 0, 0.2);
}



.footer-links a {
  margin: 0 10px;
  color: #606770;
  text-decoration: none;
}

.footer-links a:hover {
  text-decoration: underline;
}
